<template>
  <n-grid x-gap="12" :cols="2" style="height: 60px">
    <n-gi>
      <n-space justify="start" align="center" style="height: 40px">
        <n-button
          circle
          style="margin-left: 10px"
          size="medium"
          @click="goBack"
        >
          <template #icon>
            <n-icon>
              <arrow-back />
            </n-icon>
          </template>
        </n-button>
        <h2 @click="goIndex" id="logo">Comic DB</h2>
      </n-space>
    </n-gi>
    <n-gi>
      <n-space id="toolbar" justify="end" align="center">
        <n-button
          text
          style="font-size: 36px; margin-top: 8px"
          @click="showAdd = true"
        >
          <n-icon>
            <add />
          </n-icon>
        </n-button>
        <!-- <div>Hello</div> v-model:value="value"-->
        <n-auto-complete
          :options="searchOption"
          v-model:value="searchValue"
          v-on:keyup.enter="jumpToBook"
          placeholder="搜索图书"
        />
        <n-modal v-model:show="showAdd" id="add_box">
           <add-book/>
      </n-modal>
      </n-space>
    </n-gi>
  </n-grid>
</template>

<style lang="scss">
#logo {
  margin-left: 10px;
  color: #8097a3;
}
#toolbar {
  height: 80px;
  margin-right: 10px;
}
</style>

<script>
import { ArrowBack, Add } from "@vicons/ionicons5";
import { NIcon } from "naive-ui";
import AddBook from '@/components/AddBook.vue'
export default {
  created() {
    this.excuteFuzzySearch();
  },
  data() {
    return {
      searchValue: "",
      options: [],
      saveValue: "",
      showAdd: false,
      bookIdArray: [],
    };
  },
  components: {
    ArrowBack,
    NIcon,
    Add,
    AddBook
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    goIndex() {
      this.$router.push({
        name: "book.list",
        params: { query: "time", fuzzy: 0 },
      });
    },
    updateOptions() {
      fetch("/api/list", {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          query: "title",
          token: "123456",
          matcher: this.searchValue,
          fuzzy: 1,
          page: 0,
        }),
      })
        .then((data) => data.json())
        .then((data) => {
          this.bookIdArray = [];
          this.options = [];
          console.log(data);
          for (let i = 0; i < data.length; i++) {
            this.options.push(data[i].title);
            this.bookIdArray.push(data[i].book_id);
          }
        });
    },
    excuteFuzzySearch() {
      if (this.searchValue !== "" && this.searchValue !== this.saveValue) {
        this.updateOptions();
        this.saveValue = this.searchValue;
      }
      // console.log(this.options[0]);
      setTimeout(this.excuteFuzzySearch, 500);
    },
    jumpToBook() {
      if (this.options.includes(this.searchValue)) {
        this.$router.push({
          name: "book.info",
          params: {
            id: this.bookIdArray[this.options.indexOf(this.searchValue)],
          },
        });
      } else {
        this.$router.push({
          name: "book.list",
          params: {
            query: "title",
            matcher: this.searchValue,
          },
        });
      }
      this.searchValue = "";
    },
  },
  computed: {
    searchOption() {
      return this.options;
    },
  },
};
</script>