<template>
  <n-card
    style="width: 700px"
    title="添加书籍"
    :bordered="false"
    size="huge"
    role="dialog"
    aria-modal="true"
  >
    <text>E-hentai爬虫</text>
    <n-progress
      type="line"
      :status="downloadStatus"
      v-if="downloading"
      :percentage="downloadRate"
      :indicator-placement="inside"
      :processing="is_processing"
      style="margin-bottom: 10px"
    />
    <space>
      <n-input
        style="width: 480px; margin-right: 50px"
        v-model:value="ehentaiURL"
        size="small"
        round
        placeholder="https://e-hentai.org"
      />
      <n-button
        secondary
        @click="addEhentaiBook"
        type="success"
        :disabled="notAdd"
        >添加</n-button
      >
    </space>

    <!-- <template #footer>
      <n-space justify="end">
        <n-button type="success" @click="submitForm">完成</n-button>
      </n-space>
    </template> -->
  </n-card>
</template>

<script>
export default {
  setup() {},
  data() {
    return {
      downloading: true,
      downloadRate: 0,
      is_processing: false,
      downloadStatus: "default",
      ehentaiURL: "",
      notAdd: false,
      ehentaiCrawlerId: "",
    };
  },
  created() {},
  methods: {
    addEhentaiBook() {
      if (this.ehentaiURL) {
        this.sendEhentaiRequest();
      }
    },
    async sendEhentaiRequest() {
      fetch("/api/add", {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          crawler_name: "ehentai_crawler",
          token: "123456",
          url: this.ehentaiURL,
        }),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.msg === "ok") {
            this.ehentaiCrawlerId = data.uuid;
            this.crawlingStatusRequest();
          }
        });
    },
    async crawlingStatusRequest() {
      if (this.ehentaiCrawlerId) {
        fetch("/api/get_crawler_progress", {
          method: "post",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            crawler_id: this.ehentaiCrawlerId,
            token: "123456",
          }),
        })
          .then((response) => response.json())
          .then((data) => {
            if (data.msg == "ok") {
              this.is_processing = true
              this.notAdd = true
              if(parseInt(data.progress.slice(data.progress.search(/([0-9]*?%)/), -2))) {
                  this.downloadRate = parseInt(data.progress.slice(data.progress.search(/([0-9]*?%)/), -2))
              }
              setTimeout(this.crawlingStatusRequest, 500);
            } else {
              this.ehentaiCrawlerId = "";
              this.is_processing = false;
              this.notAdd = false;
            }
          });
      }
    },
  },
};
</script>