<template>
  <div vertical>
    <n-layout id="nav">
      <n-layout has-sider style="height: 90vh">
        <n-layout-sider
          bordered
          collapse-mode="width"
          :collapsed-width="64"
          :collapsed="collapsed"
          :width="240"
          :native-scrollbar="false"
          style="max-height: 100%;"
        >
          <n-menu
            :collapsed-width="64"
            :collapsed-icon-size="22"
            :options="menuOptions"
            :collapsed="collapsed"
            @update:value="handleUpdateValue"
          />
        </n-layout-sider>
        <n-layout />
      </n-layout>
    </n-layout>
  </div>
</template>

<script>
import { h, defineComponent, ref } from "vue";
import {
  NIcon,
  NLayout,
  NLayoutHeader,
  NLayoutFooter,
  NLayoutSider,
  NMenu,
  NSpace,
} from "naive-ui";
import {
  BookOutline as BookIcon,
  PersonOutline as PersonIcon,
  WineOutline as WineIcon,
  Person,
  List,
  ChevronBack,
  SettingsOutline
} from "@vicons/ionicons5";

function renderIcon(icon) {
  return () => h(NIcon, null, { default: () => h(icon) });
}
// let collapsed = false

const menuOptions = [
   {
    label: "我的",
    key: "hear-the-wind-sing",
    icon: renderIcon(Person),
  },
  {
    label: "列表",
    key: "pinball-1973",
    icon: renderIcon(List),
    disabled: false,
    children: [
      {
        label: "最近添加",
        key: "latest",
      },
      {
        label: "猜你喜欢",
        key: "rat",
      },
    ],
  },
  {
    label: "书架",
    key: "shelf",
    disabled: false,
    icon: renderIcon(BookIcon),
  },
  {
    label: "设置",
    key: "settings",
    disabled: false,
    icon: renderIcon(SettingsOutline),
  },
  {
    label: "收起",
    key: "collapsed",
    disabled: false,
    icon: renderIcon(ChevronBack),
  }
];

export default defineComponent({

  setup() {
    return {
      inverted: ref(false),
      menuOptions,
      newValue: window.clientHeight,
    };
  },
  components: {
    NLayout,
    NLayoutHeader,
    NLayoutFooter,
    NLayoutSider,
    NMenu,
    NSpace,
  },
  methods: {
    handleUpdateValue: function(key, label) {
      if(key === 'collapsed'){
        this.collapsed = !this.collapsed
        this.emitter.emit('menu_collapsed', { status: this.collapsed })
      } else if(key == 'shelf') {
        this.$router.push({name: 'book.list', params: {
          fuzzy: 0,
          query: 'time'
        }})
      }
    },
  },
  data() {
    return {
      collapsed: false
    }
  },
  watch: {

  }
});
</script>
<style lang="scss">
#nav {
  height: 100vh;
}
</style>