import { createRouter, createWebHistory } from "vue-router";

const routes = [
  {
    path: "/",
    name: "Home",
    redirect: "/list"
  },
  {
    path: "/book/:id",
    name: "book.info",
    component: () =>
      import("@/views/BookInfo.vue"),
    props: route => ({ book_id: parseInt(route.params.id) })
  },
  {
    path: "/list",
    name: "book.list",
    component: () =>
      import("@/views/BookList.vue"),
    props: route => ({
      query: route.params.query,
      fuzzy: parseInt(route.params.fuzzy),
      matcher: route.params.matcher
    })
  },
  {
    path: "/read/comic",
    name: "book.read",
    component: () =>
      import("@/views/ImageComicReader.vue"),
    props: route => ({
      slected_page: route.params.slected_page,
      book_id: parseInt(route.params.book_id)
    })
  }
]
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
